@font-face {
  font-family: "Roboto Slab";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("/build/fonts/RobotoSlab-Regular.ttf") format("truetype");
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto Slab", serif;
  line-height: 2;
}

h1,
h2,
h3 {
  text-align: center;
  margin: 10px 0;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 1.8rem;
}

a {
  text-decoration: none;
  color: #000;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

main {
  margin-top: 100px;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.about,
.categories,
.products,
.gallery,
.info {
  margin-bottom: 80px;
}

.header {
  background-color: #ffffff;
  border-bottom: 1px solid #e1e1e1;
}
.header .header__logo {
  font-size: 3rem;
  margin: 20px 0;
  user-select: none;
}
.header .header__logo span {
  color: #004065;
  text-decoration: underline;
}
.header .header__menu {
  display: none;
}
.header .nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px 0;
}

@media only screen and (width <= 576px) {
  .header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
  }
  .header .header__responsive {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  .header .header__logo {
    font-size: 2rem;
    margin: 5px 0;
  }
  .header .header__menu {
    display: block;
    filter: invert(100%);
    width: 30px;
  }
  .header .nav {
    display: none;
  }
  .header .nav.show {
    display: flex;
    flex-direction: column;
  }
  .header .nav .nav__link {
    font-size: 1.3rem;
    padding: 5px 0;
  }
}
.hero {
  background-image: url("/build/images/hero1500w.webp");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-clip: border-box;
  height: 40rem;
}

@media only screen and (width <= 576px) {
  .hero {
    background-attachment: scroll;
  }
}
.categories .categories__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.categories .categorie__image {
  height: 250px;
}
.categories .categorie__link {
  display: block;
  text-align: center;
  padding: 20px;
  font-size: 20px;
  border-bottom-right-radius: 10px;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.categories .categorie__link:hover {
  background-color: #004065;
  color: #ffffff;
}

@media only screen and (width <= 576px) {
  .categories .categories__list {
    grid-template-columns: unset;
  }
  .categories .categorie__image {
    height: 170px;
  }
  .categories .categorie__link {
    background-color: #004065;
    color: #ffffff;
  }
}
.about {
  background-color: #004065;
  height: auto;
}
.about .about__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: 50px;
}
.about .about__description {
  color: white;
}
.about .about__description .about__title {
  font-size: 2.7rem;
}

@media only screen and (width <= 992px) {
  .about .about__list {
    grid-template-columns: unset;
    gap: unset;
  }
  .about .about__description {
    padding: 50px;
  }
}
@media only screen and (width <= 576px) {
  .about .about__list {
    grid-template-columns: auto;
    gap: 0;
  }
  .about .about__description {
    padding: 50px 20px;
  }
}
.footer {
  border-top: 1px solid #e1e1e1;
}
.footer .footer__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  padding: 80px 100px;
  gap: 50px;
}
.footer .footer__title {
  text-align: left;
}
.footer .footer__menu {
  display: flex;
  flex-direction: column;
}
.footer .footer__menu .footer__link {
  text-align: left;
}
.footer .footer__copyright {
  text-align: center;
  padding: 30px 0;
  background-color: #004065;
  color: #ffffff;
}

@media only screen and (width <= 576px) {
  .footer .footer__list {
    grid-template-columns: auto;
    padding: 10px;
    gap: 10px;
  }
  .footer .footer__section {
    margin-bottom: 20px;
  }
  .footer .footer__title {
    text-align: center;
  }
  .footer .footer__menu .footer__link {
    text-align: center;
  }
}
.products .products__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
.products .product {
  background-color: #004065;
  border-radius: 10px;
}
.products .product .product__image {
  height: 250px;
}
.products .product__description {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #ffffff;
  padding: 20px;
}
.products .product__description .product__price {
  font-size: 1.8rem;
  font-weight: 900;
}

@media only screen and (width <= 992px) {
  .products .products__list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media only screen and (width <= 576px) {
  .products .products__list {
    display: flex;
    flex-direction: column;
  }
}
.gallery .gallery__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.gallery .gallery__image {
  height: 250px;
  object-fit: cover;
  transition: transform 0.3s ease;
}
.gallery .gallery__image:hover {
  cursor: pointer;
  transform: scale(1.02);
}

.overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
}
.overlay img {
  display: block;
  max-width: 70rem;
}

@media only screen and (width <= 576px) {
  .gallery .gallery__list {
    grid-template-columns: unset;
  }
}
.blog .blog__list {
  display: grid;
  grid-template-columns: 2fr 1fr;
  align-items: baseline;
  gap: 60px;
}
.blog .blog__content {
  padding: 20px 0;
}
.blog .blog__meta {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.blog .blog__meta p {
  font-weight: bold;
}
.blog .blog__meta span {
  color: #004065;
}
.blog .blog__text {
  margin-bottom: 30px;
}
.blog .blog__text p {
  margin: 10px 0;
}
.blog .blog__entry {
  border-bottom: 2px solid #e1e1e1;
  margin-bottom: 40px;
  padding-bottom: 40px;
}
.blog .blog__entry:last-of-type {
  border: none;
}
.blog .blog__list--aside {
  padding-left: 90px;
}

@media only screen and (width <= 576px) {
  .blog .blog__list {
    grid-template-columns: auto;
    gap: 10px;
    margin-bottom: 50px;
  }
  .blog .blog__meta {
    flex-direction: column;
    gap: 10px;
  }
  .blog .blog__entry:last-of-type {
    margin-bottom: 0;
    padding-bottom: 0;
  }
}
.info {
  margin-top: 50px;
}
.info .info__title {
  margin-bottom: 30px;
}
.info .info__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: 40px;
}
.info .info__image {
  border-radius: 5px;
}

@media only screen and (width <= 576px) {
  .info .info__list {
    grid-template-columns: unset;
  }
}
.form {
  max-width: 600px;
  margin: 0 auto;
}
.form fieldset {
  border: 1px solid #c2c2c2;
  margin-bottom: 20px;
}
.form legend {
  background-color: #004065;
  width: 100%;
  text-align: center;
  color: #FFF;
  text-transform: uppercase;
  font-weight: 900;
  padding: 20px;
  margin-bottom: 40px;
}
.form .field {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 20px;
}
.form .field label {
  flex-basis: 100px;
}
.form .field input:not([type=radio]),
.form .field textarea,
.form .field select {
  flex: 1;
  border: 1px solid #e1e1e1;
  border-radius: 10px;
  padding: 10px;
}

.button {
  display: block;
  background-color: #004065;
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 900;
  padding: 20px;
  transition: background-color 0.3s ease-out;
  text-align: center;
  border: 2px solid #ffffff;
  border-radius: 50px;
  width: 50%;
  margin: 10px auto;
}
.button:hover {
  background-color: #003351;
  cursor: pointer;
}

@media only screen and (width <= 576px) {
  .button {
    width: 100%;
  }
}